@import "https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;500;600;700;800;900&display=swap";
:root {
  --clr-black: #1c1c1c;
  --clr-gray: #2c2c2c;
  --clr-white: #f7f7f7;
  --clr-primary: #006bb6;
  --clr-secondary: #f7c600;
  --clr-hover: #2d3a4a;
  --clr-text: #f7c600;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Rubik, sans-serif;
}

body {
  background-color: var(--clr-gray);
  min-height: 100vh;
  color: var(--clr-text);
}

header {
  padding-block: 1rem;
  box-shadow: 0 0 2rem -1rem #00000080;
}

.nav {
  justify-content: center;
  max-width: 1000px;
  margin: 0 auto;
  padding-inline: 2rem;
  display: flex;
}

.logo {
  object-fit: contain;
  max-width: 100%;
  height: auto;
  max-height: 70px;
  margin-bottom: 1rem;
}

.nav-list {
  flex-wrap: wrap;
  align-items: center;
  gap: .5rem;
  display: flex;
}

#botonesTags {
  flex-wrap: wrap;
  justify-content: center;
  gap: 1rem;
  margin-bottom: 1rem;
  display: flex;
}

#botonesTags button {
  background-color: var(--clr-primary);
  color: var(--clr-text);
  cursor: pointer;
  border: none;
  border-radius: 10px;
  padding: .5rem 1rem;
  font-weight: 600;
  transition: all .3s;
}

#botonesTags button:hover {
  background-color: var(--clr-hover);
  transform: scale(1.1);
}

main {
  max-width: 1000px;
  margin: 0 auto;
  padding: 2rem;
}

.champion-list {
  grid-template-columns: 1fr;
  gap: 1rem;
  display: grid;
}

@media screen and (width >= 470px) {
  .champion-list {
    grid-template-columns: 1fr 1fr;
  }
}

@media screen and (width >= 700px) {
  .champion-list {
    grid-template-columns: 1fr 1fr 1fr;
  }
}

.champion {
  background-color: var(--clr-black);
  text-transform: uppercase;
  border-radius: 1rem;
  padding-block: 1rem;
  position: relative;
  overflow: hidden;
  box-shadow: 0 0 3rem -1rem #00000040;
}

.champion-img {
  justify-content: center;
  padding-inline: 1rem;
  display: flex;
  position: relative;
}

.champion-img img {
  border-radius: .5rem;
  width: 100%;
  max-width: 100%;
  height: auto;
}

.champion-info {
  text-align: center;
  background-color: var(--clr-primary);
  color: var(--clr-text);
  padding-inline: 1rem;
  flex-direction: column;
  align-items: center;
  gap: .5rem;
  padding: 1rem;
  display: flex;
}

.champion-name {
  font-size: 1.5rem;
  font-weight: 600;
}

.champion-title {
  font-size: 1rem;
}

.champion-story {
  color: var(--clr-white);
  margin-top: .5rem;
  font-size: .85rem;
  display: none;
}

.champion:hover .champion-story {
  display: block;
}

.champion:hover {
  cursor: pointer;
}
